<template>
    <el-row>
        <el-col :span="12" :offset="6">
            <h3>店铺入驻</h3>
            <el-form label-width="120px" :model="form" ref="myform" :rules="rules">
                <el-form-item label="类型" prop="type">
                    <el-select v-model="form.type" placeholder="请选择类型">
                        <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="名称" prop="name">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
                <el-form-item label="描述" prop="description">
                    <el-input type="textarea" v-model="form.description"></el-input>
                </el-form-item>
                <el-form-item label="选择城市" prop="city">
                    <el-select placeholder="请选择城市" v-model="form.city">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        >
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="银行" prop="bank">
                    <el-input v-model="form.bank"></el-input>
                </el-form-item>
                <el-form-item label="银行卡号" prop="bank_card">
                    <el-input type="number" v-model="form.bank_card"></el-input>
                </el-form-item>
                <el-form-item label="持卡人" prop="bank_person">
                    <el-input v-model="form.bank_person"></el-input>
                </el-form-item>
               <el-form-item label="联系人" prop="contact_person">
                    <el-input v-model="form.contact_person"></el-input>
                </el-form-item>
                <el-form-item label="联系方式" prop="contact_phone">
                    <el-input v-model="form.contact_phone"></el-input>
                </el-form-item>
                <el-form-item label="纳税识别号" prop="tax_Identification_number">
                    <el-input v-model="form.tax_Identification_number"></el-input>
                </el-form-item>
                <el-form-item label="logo" prop="logo">
                    <el-upload
                            class="avatar-uploader"
                            :action="upload"
                            :on-success="handleAvatarSuccess1"
                            :before-upload="beforeAvatarUpload"
                            :show-file-list="false">
                        <img v-if="form.logo" :src="form.logo" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
                <el-form-item label="身份证正面" prop="idcard1">
                    <el-upload
                            class="avatar-uploader"
                            :action="upload"
                            :on-success="handleAvatarSuccess2"
                            :before-upload="beforeAvatarUpload"
                            :show-file-list="false">
                        <img v-if="form.idcard1" :src="form.idcard1" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
                <el-form-item label="身份证反面" prop="idcard2">
                    <el-upload
                            class="avatar-uploader"
                            :action="upload"
                            :on-success="handleAvatarSuccess3"
                            :before-upload="beforeAvatarUpload"
                            :show-file-list="false"
                    >
                        <img v-if="form.idcard2" :src="form.idcard2" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
                <el-form-item label="营业执照" prop="license">
                    <el-upload
                    class="avatar-uploader"
                    :action="upload"
                    :on-success="handleAvatarSuccess4"
                    :before-upload="beforeAvatarUpload"
                    :show-file-list="false">
                        <img v-if="form.license" :src="form.license" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submit">申请注册</el-button>
                </el-form-item>
            </el-form>
        </el-col>
    </el-row>
</template>

<script>
    export default {
        name: "ShopSign",
        data(){
            let bank_card_validator=(rule,value,callback)=>{
                if(/^(\d{16}|\d{19})$/.test(value)){
                    callback();
                }else{
                    callback(new Error("银行卡号必须是16位或者19位的数字"));
                }
            };
            return {
                options:[{label: "太原", value: "太原"}, {label: "中国", value: "中国"},{label: "美国", value: "美国"},{label: "法国", value: "法国"},{label: "英国", value: "英国"},
                    {label: "俄罗斯", value: "俄罗斯"},{label: "上海", value: "上海"},{label: "北京", value: "北京"},{label: "广州", value: "广州"},{label: "深圳", value: "深圳"}],
                options2:[{label:"商家入驻",value:1},{label:"水司入驻",value:2}],
                form: {
                    type:"",
                    name:"",
                    description:"",
                    city:"",
                    bank:"",
                    bank_card:"",
                    bank_person:"",
                    contact_person:"",
                    contact_phone:"",
                    tax_Identification_number:"",
                    logo:"",
                    idcard1:"",
                    idcard2:"",
                    license:""
                },
                rules:{
                    type:{required:true,message:"请选择注册类型",trigger:"change"},
                    name:{required:true,message:"请输入店铺名称",trigger:"blur"},
                    description:{required:true,message:"请输入店铺名称",trigger:"blur"},
                    city:{required:true,message:"请选择所在城市",trigger:"change"},
                    bank:{required:true,message:"请输入银行名称",trigger:"blur"},
                    bank_card:[
                        {required:true,message:"请输入银行卡号",trigger:"blur"},
                        {validator:bank_card_validator,trigger:"blur"}
                        ],
                    bank_person:{required:true,message:"请输入持卡人",trigger:"blur"},
                    contact_person:{required:true,message:"请输入联系人",trigger:"blur"},
                    contact_phone:{required:true,message:"请输入联系电话",trigger:"blur"},
                    tax_Identification_number:{required:true,message:"请输入纳税人识别号",trigger:"blur"},
                    logo:{required:true,message:"请上传logo"},
                    idcard1:{required:true,message:"请上传身份证正面照"},
                    idcard2:{required:true,message:"请上传身份证背面照"},
                    license:{required:true,message:"请上传营业执照"}
                },
                upload:this.$api.UPLOAD
            }
        },
        methods: {
            beforeAvatarUpload(file){
                let typeArr=["image/png","image/gif","image/jpg","image/jpeg"];
                if(!typeArr.includes(file.type)){
                    this.$message.error("请选择正确的图片类型");
                    return false;
                }
                if(file.size>200*1000){
                    this.$message.error("文件大小超出闲置");
                    return false;
                }
                return true;
            },
            handleAvatarSuccess1(res){
                if(res!=="error"){
                    this.form.logo=res;
                    this.$refs.myform.validateField("logo");
                }else{
                    this.$message.error("图片上传失败");
                }
            },
            handleAvatarSuccess2(res){
                if(res!=="error"){
                    this.form.idcard1=res;
                    this.$refs.myform.validateField("idcard1");
                }else{
                    this.$message.error("图片上传失败");
                }
            },
            handleAvatarSuccess3(res){
                if(res!=="error"){
                    this.form.idcard2=res;
                    this.$refs.myform.validateField("idcard2");
                }else{
                    this.$message.error("图片上传失败");
                }
            },
            handleAvatarSuccess4(res){
                if(res!=="error"){
                    this.form.license=res;
                    this.$refs.myform.validateField("license");
                }else{
                    this.$message.error("图片上传失败");
                }
            },
            submit(){
                this.$refs.myform.validate(valid=>{
                    if(valid){
                        this.$axios.post(this.$api.SHOP,this.form).then(res=>{
                            if(res.data.code===200){
                                this.$message.success("提交成功");
                                this.$refs.myform.resetFields();
                            }
                        })
                    }else{
                        return false;
                    }
                })
            }
        },
        mounted() {
        }
    }
</script>

<style scoped lang="scss">
    .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
        border: 1px dashed #d9d9d9;
    }
    .avatar {
        width: 178px;
        height: 178px;
        display: block;
    }
</style>